@import '../assets/fonts/font.css';

.resultTitle {
    font-family: 'Asac';
    font-weight: 800;
    font-size: 2.8rem;
    color: #EEEEEE;
    text-shadow: 0.5rem 0.5rem 0 #02082c;
    
    margin: 0;
    padding-top: 3rem;
    word-break: keep-all;
    overflow-wrap: break-word;
}

.Name {
    font-size: 3.5rem;
    word-break: keep-all;
    overflow-wrap: break-word;
    padding: 0.1rem;
}

.Mark {
    font-family: 'OAGothic';
    font-size: 0.8rem;
    text-align: right;
    color: #02082c;
    text-shadow: none;
    padding: 0;
    margin: 0;
}

.sub {
    font-family: 'OAGothic';
    font-weight: 800;
    font-size: 1.8rem;
    color: #EEEEEE;
    text-shadow: 0.1rem 0.1rem 0 #02082c;
    word-break: keep-all;
    overflow-wrap: break-word;
    margin: 0.3rem;
    padding: 0;
}

.explain {
    width: 30rem;
    padding: 0.5rem;
    margin: 0 auto;
    background-color: #EEEEEE;
    box-shadow: 0.1rem 0.1rem 0.3rem 0 #02082c;
    
    border-radius: 0.2rem;


    word-break: keep-all;
    overflow-wrap: break-word;
}

#capture {
    background-color: #EEEEEE;
    margin: 0;
    padding: 0.2rem;
}

.charmList {
    font-family: 'OAGothic';
    font-weight: 500;
    font-size: 1rem;
    color: #02082c;
    margin: 0.2rem;
    padding: 0;


    word-break: keep-all;
    overflow-wrap: break-word;
}

.shareBox {
    text-align: center;
    padding: 0.2rem;
}

.OshiChart {
    display: flex;
    align-items: center;
    margin: 0;
    margin-top: 0.5rem;
}

.per {
    font-family: 'OAGothic';
    font-weight: 800;
    font-size: 1rem;
    margin: 0;
    padding: 0;
}

.FeedbackBtn {
    background-color: #EEEEEE;
    border-color: transparent;
    border-radius: 3rem;
    width: 7rem;
    box-shadow: 0.2rem 0.2rem 0 0 #02082c;


    padding: 0.1rem;
    margin: 0.2rem;


    font-family: 'OAGothic';
    font-size: 1.2rem;
    font-weight: 800;
    color:#02082c;
}

.goTest {
    background-color: #EEEEEE;
    border-color: transparent;
    border-radius: 3rem;
    width: 20rem;
    box-shadow: 0.3rem 0.3rem 0 0 #02082c;


    padding: 0.1rem;
    margin-top: 0rem;


    font-family: 'OAGothic';
    font-size: 2.1rem;
    font-weight: 800;
    color:#02082c;


    word-break: keep-all;
    overflow-wrap: break-word;
}